import React, { Component } from 'react';

import getStartedBottomLeftDesktop from '../assets/imgs/get-started-bottom-left-desktop.png';
import getStartedBottomLeftMobile from '../assets/imgs/get-started-bottom-left-mobile.png';

export default class CallToAction extends Component {

    render() {
        return (
            <div className="flex call-to-action">
                <div className="bottom-left-picture-desktop-container">
                    <img src={getStartedBottomLeftDesktop} />
                </div>
                <div className="bottom-left-picture-mobile-container">
                    <img src={getStartedBottomLeftMobile} />
                </div>
                <div className="flex main-container call-to-action-container">
                    <div className="title call-to-action-title">
                        Ready to get started?
                    </div>
                    <div className="flex center align-center pointer btn cta-contact">
                        <span>contact us</span>
                    </div>
                </div>
            </div>
        );
    }
}