import React, { Component } from 'react';

import heroLeft from '../assets/imgs/hero-left-img.png';
import herobottom from '../assets/imgs/hero-bottom-img.png';
import heroRectangle from '../assets/imgs/hero-rectangle-img.png';

export default class Hero extends Component {

    render() {
        return (
            <div className="flex hero">
                <div className="hero-left-pic-container">
                    <img src={heroLeft} />
                </div>
                <div className="main-container">
                    <div className="flex hero-container">
                        <div className="hero-title">
                            Find the best <span className="hero-emphasis">talent</span>
                        </div>
                        <div className="flex column space-between">
                            <div className="hero-rectangle-container">
                                <img src={heroRectangle} />
                            </div>
                            <p className="hero-text">
                                Finding the right people and building high performing teams can be hard.
                                Most companies aren’t tapping into the abundance of global talent.
                                We’re about to change that.
                        </p>
                        </div>
                    </div>
                    <div className="hero-bottom-container-desktop">
                        <img src={herobottom} />
                    </div>
                </div>
                <div className="hero-bottom-container-mobile">
                    <img src={herobottom} />
                </div>
            </div>
        );
    }
}