import React, { Component } from 'react';

import logo from '../assets/imgs/logo.svg';
import iconFacebook from '../assets/imgs/icon-facebook.svg';
import iconPinterest from '../assets/imgs/icon-pinterest.svg';
import iconTwitter from '../assets/imgs/icon-twitter.svg';

export default class Footer extends Component {

    render() {
        return (
            <div className="footer">
                <div className="main-container">
                    <div className="flex footer-subcontainer">
                        <div className="flex column footer-menu">
                            <div className="footer-logo-container">
                                <img src={logo} />
                            </div>
                            <div className="flex footer-menu-buttons">
                                <div className="pointer">home</div>
                                <div className="pointer">about</div>
                            </div>
                        </div>
                        <div className="address-container">
                            987  Hillcrest Lane<br></br>
                            Irvine, CA<br></br>
                            California 92714<br></br>
                            Call Us : 949-833-7432<br></br>
                        </div>
                        <div className="flex column">
                            <div className="flex social-container">
                                <div className="pointer icon-container">
                                    <img src={iconFacebook} />
                                </div>
                                <div className="pointer icon-container">
                                    <img src={iconPinterest} />
                                </div>
                                <div className="pointer icon-container">
                                    <img src={iconTwitter} />
                                </div>
                            </div>
                            <div className="copyright-container">
                                Copyright 2020. All Rights Reserved
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}